.searchLabel {
    display: inline-block;
}

.compact.selection.dropdown {
    width: 100px;
}

.item:first-child {
    border-top: none !important;
}

.item h1 {
    display: inline-block;
}

.ui.items>.item .meta {
    font-size: 1.3em;
}

a.header span {
    color: #b9b9b9;
}

.ui.toggle.checkbox {
    margin-bottom: 1em;
}

.ui.input.fluid.error input {
    background-color: #fff6f6 ;
    border-color: #e0b4b4 ;
    color: #9f3a38 ;
}



.ui.inline.loader {
    position: absolute;
    vertical-align: middle;
    margin: 0;
    left: 50% !important;
    transform: translateX(-50%) !important;
    top: 50% !important;
    transform: translateY(-50%) !important;

}